	module.exports = {"data":{"markdownRemark":{"html":"<h2>Adding Fractions</h2>\n<p>A fraction is generally used to represent a number which is a ratio of two numbers.\n<br>Consider the fraction <span class=\"fraction\"><sup>4</sup>⁄<sub>5</sub></span>, here 4 is called the numerator and 5 is called the denominator. </p>\n<h4>Adding fractions with common denominator</h4>\n<p>Add the numerators of both fractions and put the resultant over the denominator.</p>\n<h6>Example 1</h6>\n<p>Consider fractions <span class=\"fraction\"><sup>4</sup>⁄<sub>5</sub></span> and <span class=\"fraction\"><sup>3</sup>⁄<sub>5</sub></span></p>\n<ol>\n<li>The denominator of the fractions is common.</li>\n<li>The numerators are 4 and 3</li>\n<li>Add the numerators, 4 + 3 = 7</li>\n<li>Place the resultant over the common denominator</li>\n<li>Simplify the resultant fraction, if possible</li>\n</ol>\n<pre>  <span class=\"fraction\"><sup>4</sup>⁄<sub>5</sub></span> + <span class=\"fraction\"><sup>3</sup>⁄<sub>5</sub></span> = <span class=\"fraction\"><sup>7</sup>⁄<sub>5</sub></span> </pre>\n<h6>Example 2</h6>\n<pre>  <span class=\"fraction\"><sup>5</sup>⁄<sub>16</sub></span> + <span class=\"fraction\"><sup>3</sup>⁄<sub>16</sub></span> = <span class=\"fraction\"><sup>8</sup>⁄<sub>16</sub></span> (Simplifying it further, <span class=\"fraction\"><sup>8</sup>⁄<sub>16</sub></span> = <span class=\"fraction\"><sup>1</sup>⁄<sub>2</sub></span>) </pre>\n<h4>Adding fractions with different denominators</h4>\n<ol>\n<li>\n<p>Convert the fractions to equivalent fractions with common denominator</p>\n</li>\n<li>\n<p>To convert two fractions to common denominator, multiply the numerator and denominator of a fraction with the denominator of the other fraction.</p>\n</li>\n<li>\n<p>Now that the fractions have common denominators, add the numerators of both fractions and put the resultant over the denominator</p>\n<p>Consider, <span class=\"fraction\"><sup>a</sup>⁄<sub>b</sub></span> and <span class=\"fraction\"><sup>c</sup>⁄<sub>d</sub></span>  are fractions with different denominators, you can add these fractions in a single step like below,\n<br></p>\n<pre>  <span class=\"fraction\"><sup>a</sup>⁄<sub>b</sub></span> + <span class=\"fraction\"><sup>c</sup>⁄<sub>d</sub></span> = <span class=\"fraction\"><sup>(a * d) + (b * c)</sup>⁄<sub>(b * d)</sub></span> </pre>\n</li>\n</ol>\n<h6>Example</h6>\n<p>Consider fractions <span class=\"fraction\"><sup>5</sup>⁄<sub>6</sub></span> and <span class=\"fraction\"><sup>5</sup>⁄<sub>15</sub></span></p>\n<ol>\n<li>The denominators are different. So you need to make the denominators common</li>\n<li>Before that, if possible, simplify the fractions. In this case, <span class=\"fraction\"><sup>5</sup>⁄<sub>15</sub></span> can be simplified as <span class=\"fraction\"><sup>1</sup>⁄<sub>3</sub></span>. Here <span class=\"fraction\"><sup>5</sup>⁄<sub>15</sub></span> and <span class=\"fraction\"><sup>1</sup>⁄<sub>3</sub></span> are called equivalent fractions.</li>\n<li>After simplification, the fractions are <span class=\"fraction\"><sup>5</sup>⁄<sub>6</sub></span> and <span class=\"fraction\"><sup>1</sup>⁄<sub>3</sub></span>.</li>\n<li>Now to add these fractions, you must make the denominators common</li>\n<li>Multiply the numerator and denominator of a fraction with denominator of the other</li>\n<li>For fraction <span class=\"fraction\"><sup>5</sup>⁄<sub>6</sub></span> , the denominator of the other fraction is 3. For fraction <span class=\"fraction\"><sup>1</sup>⁄<sub>3</sub></span> , the denominator of the other fraction is 6</li>\n</ol>\n<pre>  <span class=\"fraction\"><sup>((5 * 3) + (1 * 6))</sup>⁄<sub>(6 * 3)</sub></span> = <span class=\"fraction\"><sup>21</sup>⁄<sub>18</sub></span> </pre>\n<ol start=\"7\">\n<li>The resultant fraction is <span class=\"fraction\"><sup>21</sup>⁄<sub>18</sub></span>. This can be further simplified as <span class=\"fraction\"><sup>7</sup>⁄<sub>6</sub></span></li>\n</ol>\n<pre>  <span class=\"fraction\"><sup>5</sup>⁄<sub>6</sub></span> + <span class=\"fraction\"><sup>5</sup>⁄<sub>15</sub></span> = <span class=\"fraction\"><sup>7</sup>⁄<sub>6</sub></span> </pre>","fields":{"slug":"/mathematics/adding-fractions/"},"frontmatter":{"title":"Adding Fractions"}}},"pathContext":{"slug":"/mathematics/adding-fractions/"}}

